import React, { Component } from "react"
import { connect } from "react-redux"
import { changeAuth } from "../actions/changeAuth"
import { Formik, Form, Field, ErrorMessage } from "formik"
import steps from "../images/steps.png"
import * as Yup from "yup"
import { navigate } from "gatsby"
import { setCategories } from "../actions/setCategories"
import { getEvents } from "../actions/getEvents"
import Cookies from "js-cookie"

import auth from "../components/authValidation.module.scss"

const SignupSchema = Yup.object().shape({
  email: Yup.string()
    .email("Invalid email")
    .required("Required"),
  password: Yup.string().required("Required"),
})

class UserAuth extends Component {
  userSignIn = async (email, password, resetForm) => {
    const data = await fetch(process.env.API + "signIn", {
      body: JSON.stringify({ email: email, password: password }),
      mode: "cors", // no-cors, cors, *include
      cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
      credentials: "include", // include, *include, omit
      headers: {
        "content-type": "application/json",
        "csrf-token": this.props.token,
      },
      method: "POST",
    }).catch(error => this.setState({ isError: true }))
    if (data.ok) {
      console.log(data.ok)
      data.json().then(content => {
        console.log(content)
        this.props.changeAuth(content.authStatus)
        this.props.setCategories(content.user.categories)
        this.props.getEvents(content.user.events)
        navigate("/")
        resetForm()
      })
    } else {
      this.setState({
        isError: true,
      })
    }
  }

  check = () => {
    alert(this.props.token)
  }

  render() {
    return (
      <div className={auth.authBox}>
        <img className={auth.steps} src={steps} />
        <div className={auth.innerBox}>
          <Formik
            initialValues={{ email: "", password: "" }}
            validationSchema={SignupSchema}
            onSubmit={(values, { resetForm }) => {
              this.userSignIn(values.email, values.password, resetForm)
            }}
          >
            {({ errors, touched }) => (
              <Form>
                <Field placeholder="Email" name="email" />
                {errors.email && touched.email ? (
                  <p className={auth.errorMessage}>{errors.email}</p>
                ) : null}
                <Field type="password" placeholder="Password" name="password" />
                {errors.password && touched.password ? (
                  <p className={auth.errorMessage}>{errors.password}</p>
                ) : null}
                <button type="submit">Submit</button>
              </Form>
            )}
          </Formik>
        </div>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return { isAuthed: state.authStatus, token: state.token }
}

export default connect(
  mapStateToProps,
  { changeAuth, getEvents, setCategories }
)(UserAuth)
